section.api {
  margin-left: $sidebar-width;

  header {
    h1 {
      letter-spacing: .15rem;
      color: $white;
      text-shadow: 0px 0px 4px rgba(0, 0, 0, 0.7);
      font-size: 42px;
      font-weight: bold;
      padding: 16px 0 16px 48px;
      background: linear-gradient(135deg, $primary-color 0%, $accent-color 100%);
    }

    h2 {
      font-size: 18px;
      line-height: 26px;
      font-weight: 200;
      padding: 16px 48px;
      background: $panel-color;
      box-shadow: -20px 0 14px 0 rgba(0, 0, 0, 0.1), inset 0 -1px 0 rgba(0, 0, 0, 0.1);
    }
  }

  > article {
    margin: 32px 48px;

    .api-category-header {
      color: $accent-color;
      font-size: 24px;
      line-height: 1.225;
      border-bottom: 1px solid rgba(0,0,0,.12);
      padding-bottom: .3em;
      margin-bottom: 16px;
    }

    .api-category-list {
      columns: 2;
      margin-bottom: 48px;

      li {
        font-size: 20px;
        line-height: 32px;
        margin-bottom: 4px;
        background-position: left center;
        background-repeat: no-repeat;
        padding-left: 42px; /* Adjust according to image size to push text across. */

        a {
          color: $primary-color;
          opacity: .8;
          text-decoration: none;
        }
      }

      .class-item {
        background-image: url('./images/api/class.svg');
      }

      .interface-item {
        background-image: url('./images/api/interface.svg');
      }

      .constant-item {
        background-image: url('./images/api/field.svg');
      }

      .function-item {
        background-image: url('./images/api/method.svg');
      }
    }

    .api-card {
      border: 1px solid #dadada;
      box-shadow: 0 1px 2px 0 rgba(0,0,0,.1);
      background: $code-background;
      margin-bottom: 48px;

      blockquote {
        padding: 32px;

        .api-signature {
          border-bottom: 1px solid rgba(0,0,0,.12);
          margin-bottom: 8px;
          padding-bottom: 8px;

          font-family: 'Source Code Pro';
          color: $token-keyword;
        }
      }

      .param-name {
        color: $token-string;
      }

      .param-type, .api-type {
        color: $token-function;
      }

      .function-returns {
        color: $token-function;
      }

      .api-description, .param-comment {
        line-height: 1.4;
        font-size: 16px;
        color: $primary-text-color;
      }

      article {
        margin: 0 32px;

        h4, h4.function-returns {
          margin: 16px 0;
          font-size: 20px;
          color: $accent-color;
        }

        .param-comment {
          margin-top: 8px;
        }

        .function-params {
          margin-bottom: 24px;
        }

        .declare-param {
          font-family: 'Source Code Pro';
          color: $token-keyword;
        }
      }
    }
  }
}
